import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: "app-landingpage",
  templateUrl: "landingpage.component.html"
})
export class LandingpageComponent implements OnInit, OnDestroy {
  isCollapsed = true;

  // Store all languages
  courses =[];
  Lessons = [];
  Languages = [];
  WordAssociation = [];
  Errormessage;

  StartedCourses;

  //Disable Button
  Hide = 'disabled'


  // Variable to activate popup messages
  errclass;
  errrole;
  class;
  role;

  // Store Message from serve
  ErrormessageCourse;

  //tabColours
  text ='#00f2c3;';
  // LoadingLesson dropdow
  LessonDropDown = [];
  // Display user email
  email;
  constructor(private _router: Router, private http: HttpClient) {}

  ngOnInit() {
    // Checking user role and verify token
    this.VerifyToken();

    this.getLanguageData();
    this. CourseStarted();
    var body = document.getElementsByTagName("body")[0];
    body.classList.add("landing-page");

  }

  getLanguageData() {
    this.http.get('http://167.71.93.67:8080/api/languagesLesson').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
      // tslint:disable-next-line: forin
      this.Languages = [];
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallLanguages = message[i].Language;
        var getallLessonID = message[i].lessonId;
        this.Languages.push({Language: getallLanguages, lessonId:getallLessonID });
      }
    });
    
  }

  // Remove Course
  Delete(Language, course, Lesson){
    const DeleteData = {Language:Language,course:course, Lesson:Lesson }
    this.http.post('http://167.71.93.67:8080/api/DeleteStartedCourse',JSON.stringify(DeleteData)).subscribe(response =>{
      const message = JSON.parse(JSON.stringify(response));
      this.errclass = 'alert alert-danger';
          this.errrole = 'alert';
          this.ErrormessageCourse = message.message;
          this.CourseStarted();
    })
  }
  getCoursesData() {
    this.http.get('http://167.71.93.67:8080/api/courses').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallcourses = message[i].courses;
        this.courses.push({ courses: getallcourses });
      }
    });
  }

  // Get all word Association data from server and display on the screen
  getLessonData(change) {
    if( change == 'Word Association'){
      this.http.get('http://167.71.93.67:8080/api/wordassociation').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
      // tslint:disable-next-line: forin
      this.WordAssociation = [];
      // tslint:disable-next-line: forin
      // looping through all data from response and same to array
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
            const getId = message[i].Id;
            var option1 = message[i].optionone;
            var option2 = message[i].optiontwo;
            var option3 = message[i].optionthree;
            var Answer = message[i].Answer;
            var Path = message[i].ImageName;
            this.WordAssociation.push({Id: getId , option1: option1, option2: option2, option3: option3 , Answer:Answer , Path:Path});
          }
        });

      } else {
        alert('Sentence construction is triggered')
      }
  }

  // Get Lesson ID
  GetLessonID(GetLessonId){
    for (const i in this.courses) {
      if (this.courses[i].courses == GetLessonId){
        localStorage.setItem('LesssonId', this.courses[i].LessonID);
      }
    }
    /* localStorage.setItem('LesssonId', GetLessonId);
    this.ChangeCourse(); */
    this.ChangeCourse();
  }

  // Get All started Course for a certain user
  CourseStarted(){
    const email = {email: localStorage.getItem('email')}
    this.http.post('http://167.71.93.67:8080/api/GetStartedCourses',JSON.stringify(email)).subscribe(response =>{
      const data = JSON.parse(JSON.stringify(response));
      console.log(data)
      this.StartedCourses = [];
      for (var i in data) {
        var email= data[i].email;
        var languag = data[i].languag;
        var coursename = data[i].coursename;
        var Lesson = data[i].Lesson;
        var Started_date = data[i].Started_date;
        this.StartedCourses.push({email: email ,languag: languag, coursename:coursename, Lesson:Lesson , Started_date:Started_date});
      }
    })
  }

  // Filter languages and course 
  ChangeLanguage(change) {
    const Language = {Language: change};
    this. FilterLesson(Language);
    this.http.post('http://167.71.93.67:8080/api/filterbyLanguageLesson', JSON.stringify(Language)).subscribe(response => {
      const message = JSON.parse(JSON.stringify(response));
      // tslint:disable-next-line: forin
      this.courses = [];
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallcourses = message[i].courses;
        var getallLessonId= message[i].LessonID ;
        this.courses.push({ courses: getallcourses, LessonID:getallLessonId  });
      }
    });
  }
  //Filter Lesson by Course
  ChangeCourse() {
    this.LessonDropDown = [];
    this.LessonDropDown.push({ Word: 'Word Association', Sentance: 'Sentance Construction'});
  }

  StartCourse(coursevalues){
    const value  =  coursevalues.i
    if (coursevalues.value.language === '' || coursevalues.value.coursename === '' || coursevalues.value.Lesson === ''){
      alert('Not Ready to go')
    } else {
      // Collect user details and save it to Database
      const UserCourseDetails = {email: this.email, Language: coursevalues.value.language, coursename:coursevalues.value.coursename, Lesson: coursevalues.value.Lesson}
      this.http.post('http://167.71.93.67:8080/api/UserCourseDetails', JSON.stringify(UserCourseDetails)).subscribe(response => {
        const message = JSON.parse(JSON.stringify(response));
        if (message.message){
          this._router.navigate(['/startcourse']);
        } else {
          this.class = 'alert alert-danger';
          this.role = 'alert';
          this.Errormessage = ['You can not do same course twice, remove it and start again'];
        }
      })
    }
  }

  //Filter Lesson by Language
  FilterLesson(change) {
    this.http.post('http://167.71.93.67:8080/api/filterLessonsbyLanguage', JSON.stringify(change)).subscribe(response => {
      const message = JSON.parse(JSON.stringify(response));
      // tslint:disable-next-line: forin
      this.Lessons = [];
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallLesson = message[i].Lesson;
        let getallLanguages = message[i].Language;
        let getallcourse = message[i].course;
        this.Lessons.push({ Lesson: getallLesson, Language: getallLanguages, course: getallcourse});
      }
    });
  }
  fetchNews(active){
    this.text = 'success';
  }
    // Logout user
    logout(){
      localStorage.clear();
      this._router.navigate(['/']);
    }

  //Verifytoken and also check user role
  VerifyToken() {
    const Token = {token: localStorage.getItem('token')};
    const role = {role: localStorage.getItem('role')};
    if (localStorage.getItem('token')) {
        this.http.post('http://167.71.93.67:8080/api/authverification', JSON.stringify(Token)).subscribe(response=>{
        const token = JSON.parse(JSON.stringify(response));
          // tslint:disable-next-line: triple-equals
        if ( role.role != token.role ) {
          localStorage.clear();
          this._router.navigate(['/']);
        } else {
          if(!role){
            localStorage.clear();
            this._router.navigate(['/']);
          } else {
            this.email = localStorage.getItem('email');
          }
        }
        });
    } else {
      localStorage.clear();
      this._router.navigate(['/']);
    }
  }

  ngOnDestroy() {
    var body = document.getElementsByTagName("body")[0];
    body.classList.remove("landing-page");
  }
}
