import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router'; 

@Component({
  selector: 'app-lessons',
  templateUrl: './lessons.component.html',
  styleUrls: ['./lessons.component.scss']
})
export class LessonsComponent implements OnInit, OnDestroy {
  isCollapsed = true;
  Errormessage;
  email;
  courses =[];
  Lessons = [];
  Languages = [];
  //add class and role to message alert
  role;
  class;
  constructor( private _router: Router, private http: HttpClient) { }

  ngOnInit() {
    // tslint:disable-next-line: prefer-const
    let body = document.getElementsByTagName('body')[0];
    body.classList.add('lesson-page');
    this.VerifyToken();
    this.getLessonData();
    this.getLanguageData();
    // Loading data with ajax
  }
  logout(){
    localStorage.clear();
    this._router.navigate(['/']);
  }
  getLanguageData() {
    this.http.get('http://167.71.93.67:8080/api/languagesData').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
      // tslint:disable-next-line: forin
      this.Languages = [];
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallLanguages = message[i].Language;
        this.Languages.push({Language: getallLanguages});
      }
    });
    
  }
  getCoursesData() {
    this.http.get('http://167.71.93.67:8080/api/courses').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallcourses = message[i].courses;
        this.courses.push({ courses: getallcourses });
      }
    });
  }
  ChangeLanguage(change) {
    const Language = {Language: change};
    this. FilterLesson(Language);
    this.http.post('http://167.71.93.67:8080/api/filterbyLanguage', JSON.stringify(Language)).subscribe(response => {
      const message = JSON.parse(JSON.stringify(response));
      // tslint:disable-next-line: forin
      this.courses = [];
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallcourses = message[i].courses;
        this.courses.push({ courses: getallcourses });
      }
    });
  }
  //Filter Lesson by Course
  ChangeCourse(change) {
    const Course = {Course: change};
    this.http.post('http://167.71.93.67:8080/api/filterLessonsbycourse', JSON.stringify(Course)).subscribe(response => {
      const message = JSON.parse(JSON.stringify(response));
      // tslint:disable-next-line: forin
      this.Lessons = [];
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallLesson = message[i].Lesson;
        let getallLanguages = message[i].Language;
        let getallcourse = message[i].course;
        this.Lessons.push({ Lesson: getallLesson, Language: getallLanguages, course: getallcourse});
      }
    });
  }

  //Filter Lesson by Language
  FilterLesson(change) {
    this.http.post('http://167.71.93.67:8080/api/filterLessonsbyLanguage', JSON.stringify(change)).subscribe(response => {
      const message = JSON.parse(JSON.stringify(response));
      // tslint:disable-next-line: forin
      this.Lessons = [];
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallLesson = message[i].Lesson;
        let getallLanguages = message[i].Language;
        let getallcourse = message[i].course;
        this.Lessons.push({ Lesson: getallLesson, Language: getallLanguages, course: getallcourse});
      }
    });
  }


  createLesson(CreateLesson) {
    this.class = 'alert alert-danger';
    this.role = 'alert';
    // tslint:disable-next-line: no-conditional-assignment
    // tslint:disable-next-line: triple-equals
    if ( CreateLesson.value.Lesson == '' || CreateLesson.value.Lesson == 'Select Lesson') {
      this.Errormessage = 'Please fill the form';
    } else {
      // tslint:disable-next-line: triple-equals
      if (CreateLesson.value.language == '' || CreateLesson.value.coursename == '' || CreateLesson.value.coursename == 'Select Course' ) {
        this.Errormessage = 'Please fill the form';

      } else {
        this.http.post('http://167.71.93.67:8080/api/lasson', JSON.stringify(CreateLesson.value)).subscribe(response => {
        const message = JSON.parse(JSON.stringify(response));
        this.VerifyToken();
        this.getLessonData();
        this.Errormessage = message.message;
        localStorage.setItem('LessonID', message.LessonID);
        localStorage.setItem('LessonLanguage', message.Language);
        localStorage.setItem('CourseName', message.CourseName )
        console.log(message)
          if(CreateLesson.value.Lesson == 'Word Association'){
            if (message.Status == true){
               this._router.navigate(['/wordAssociation']);
            }
          } else {
            alert('Page not Created yet')
          }
          CreateLesson.resetForm();
        });
      } 
    }
  }
  ngOnDestroy() {
    // tslint:disable-next-line: prefer-const
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove('lesson-page');
  }
//Verifytoken and also check user role
VerifyToken() {
  const Token = {token: localStorage.getItem('token')};
  const role = {role: localStorage.getItem('role')};
  if (localStorage.getItem('token')) {
      this.http.post('http://167.71.93.67:8080/api/authverification', JSON.stringify(Token)).subscribe(response=>{
      const token = JSON.parse(JSON.stringify(response));
        // tslint:disable-next-line: triple-equals
      if ( role.role == token.role ) {
        if(token.role == true)
        {
          this.email = localStorage.getItem('email');
        } else {
          localStorage.clear();
          this._router.navigate(['/']);
        }
      } else {
        localStorage.clear();
        this._router.navigate(['/']);
      }
    });
  } else {
    localStorage.clear();
    this._router.navigate(['/']);
  }
}

  getLessonData() {
    this.http.get('http://167.71.93.67:8080/api/Lessons').subscribe(data => {
      const message = JSON.parse(JSON.stringify(data));
      this.Lessons = [];
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallLesson = message[i].Lesson;
        let getallLanguages = message[i].Language;
        let getallcourse = message[i].course;
        this.Lessons.push({ Lesson: getallLesson, Language: getallLanguages, course: getallcourse});
      }
    });
  }

}
