import { Component, OnInit, OnDestroy, HostListener } from "@angular/core";
import { FormBuilder,Validators, FormGroup } from '@angular/forms';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: "app-registerpage",
  templateUrl: "registerpage.component.html"
})

export class UserRegistration implements OnInit, OnDestroy {

  firstName;
  surname;
  dateofbirth;

  //display message if theres  error
  message;

  //add class and role to message alert
  role;
  class;

  // Array to hold registered user
  RegisterUserData = {}

  submitted = false;
  isCollapsed = true;
  focus;
  focus1;
  focus2;
  registerFrom: FormGroup;
  private url = 'http://167.71.93.67:8080/api';
  constructor(private http: HttpClient, private formbuilder: FormBuilder) {

  }
  ngOnInit() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('register-page');
    // tslint:disable-next-line: deprecation
    this.onMouseMove(event);
  }

  submit(Registratiomform){
    this.class = 'alert alert-danger';
    this.role = 'alert';
    // tslint:disable-next-line: triple-equals
    if ( Registratiomform.value.Password == '' || Registratiomform.value.ConfirmPassword == '' ) {
      this.message = 'Please fill all the fields';
    // tslint:disable-next-line: triple-equals
    } else if ( Registratiomform.value.firstName == '' || Registratiomform.value.Surname == '') {
      this.message = 'Please fill all the fields';
      // tslint:disable-next-line: triple-equals
    } else if ( Registratiomform.value.Email == '' || Registratiomform.value.DateofBirth == '' ) {
      this.message = 'Please fill all the fields';
    } else {
      // tslint:disable-next-line: triple-equals
      if (Registratiomform.value.Password == Registratiomform.value.ConfirmPassword){
        this.http.post('http://167.71.93.67:8080/api/users', JSON.stringify(Registratiomform.value)).subscribe(response=>{
          const message = JSON.parse(JSON.stringify(response));
          this.message = message.message;
          Registratiomform.resetForm();
        });
      } else {
        this.message = 'Password is not matching';
      }
    }
  }
  @HostListener('document:mousemove', ['$event'])
  onMouseMove(e) {
  }
  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('register-page');
  }


  // Collecting Data
  RegisterUser() {
    console.log(this.RegisterUserData);
  }
}
