import { Component, OnInit, OnDestroy } from "@angular/core";
import { Router } from '@angular/router'; 
import { FormBuilder,Validators, FormGroup } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';
import { count } from 'console';

@Component({
  selector: 'app-profilepage',
  templateUrl: 'createlanguage.component.html',
})
export class CreatelanguagepageComponent implements OnInit, OnDestroy {

  p = 1;
  isCollapsed = true;
  dataRefresher: any;
  Languagename;
  Datecreated;
  email;
  Successmessage;
  Languages = [];

  //add class and role to message alert
  role;
  class;
  role1;
  class1;

  // array of all items to be paged
  allItems: any[];
  constructor(private _router: Router, private http: HttpClient) {

  }

  logout(){
    localStorage.clear();
    this._router.navigate(['/']);
  }

  createLanguage(CreateLanguage) {
    this.class = 'alert alert-danger';
    this.role = 'alert';
    // tslint:disable-next-line: no-conditional-assignment
    // tslint:disable-next-line: triple-equals
    if ( CreateLanguage.value.Language == '' || CreateLanguage.value.Language == null) {
      this.Languagename = 'Please fill the form';
    } else {
      this.http.post('http://167.71.93.67:8080/api/language', JSON.stringify(CreateLanguage.value)).subscribe(response=>{
        CreateLanguage.resetForm();

        let message = JSON.parse(JSON.stringify(response));
        this.Languagename = message.message;
        this.getData();
      });
    }
  }

  ngOnInit() {
    // tslint:disable-next-line: prefer-const
    let body = document.getElementsByTagName('body')[0];
    body.classList.add('profile-page');
    this.getData();
    this.VerifyToken();

    // Loading data with ajax

  }
  ngOnDestroy() {
    // tslint:disable-next-line: prefer-const
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove('profile-page');
  }
  getData() {
    this.http.get('http://167.71.93.67:8080/api/languages').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
      // tslint:disable-next-line: forin
      this.Languages = [];
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallLanguages = message[i].Language;
        this.Languages.push({Language: getallLanguages});
      }
    });
  }

  //Activate languages
  AcitvateLanguage(LanguageLoad) {
    const Language = {Language: LanguageLoad };
    this.http.post('http://167.71.93.67:8080/api/LanguageActivate', Language ).subscribe( response => {
      const message = JSON.parse(JSON.stringify(response));
      this.class1 = 'alert alert-danger';
      this.role1 = 'alert';
      this.Successmessage = message.LanguageMessage;
    });
  }

  VerifyToken() {
    const Token = {token: localStorage.getItem('token')};
    const role = {role: localStorage.getItem('role')};
    if (localStorage.getItem('token')) {
        this.http.post('http://167.71.93.67:8080/api/authverification', JSON.stringify(Token)).subscribe(response=>{
        const token = JSON.parse(JSON.stringify(response));
          // tslint:disable-next-line: triple-equals
        if ( role.role == token.role ) {
          if(token.role == true)
          {
            this.email = localStorage.getItem('email');
          } else {
            localStorage.clear();
            this._router.navigate(['/']);
          }
        } else {
          localStorage.clear();
          this._router.navigate(['/']);
        }
      });
    } else {
      localStorage.clear();
      this._router.navigate(['/']);
    }
  }
}
