import { Component, OnInit, OnDestroy } from "@angular/core";
import noUiSlider from "nouislider";
import { FormBuilder,Validators, FormGroup } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';
import { Router } from '@angular/router'; 

@Component({
  selector: "app-index",
  templateUrl: "index.component.html"
})
export class IndexComponent implements OnInit, OnDestroy {


  Login: FormGroup;

  //display message if theres  error
  message;

  //add class and role to message alert
  role;
  class;

  router:Routes =[  { path: "home", component: IndexComponent }];
  loginUserData = {}
  isCollapsed = true;
  focus;
  focus1;
  focus2;

  date = new Date();
  pagination = 3;
  pagination1 = 1;
  constructor(private http: HttpClient, private formbuilder: FormBuilder, private _router: Router ) {}
  
 

  scrollToDownload(element: any) {
    element.scrollIntoView({ behavior: "smooth" });
  }
  ngOnInit() {
    var body = document.getElementsByTagName("body")[0];
    body.classList.add("index-page");

    var slider = document.getElementById("sliderRegular");

    noUiSlider.create(slider, {
      start: 40,
      connect: false,
      range: {
        min: 0,
        max: 100
      }
    });

    var slider2 = document.getElementById("sliderDouble");

    noUiSlider.create(slider2, {
      start: [20, 60],
      connect: true,
      range: {
        min: 0,
        max: 100
      }
    });
  }
  ngOnDestroy() {
    var body = document.getElementsByTagName("body")[0];
    body.classList.remove("index-page");
  }

  //check if token still available
  isLoggeding(Login){

  }
  login(LoginForm){
    localStorage.removeItem('token');
    this.http.post('http://167.71.93.67:8080/api/auth',JSON.stringify(LoginForm.value)).subscribe(response=>{
      const token = JSON.parse(JSON.stringify(response));
      if ( token && token.accessToken){
        if (token.role) {
          localStorage.setItem('token', token.accessToken);
          localStorage.setItem('role', token.role);
          localStorage.setItem('email', LoginForm.value.Email);
          this._router.navigate(['/createLanguage']);
        } else {
          localStorage.setItem('token', token.accessToken);
          localStorage.setItem('role', token.role);
          localStorage.setItem('email', LoginForm.value.Email);
          this._router.navigate(['/Landingpage']);
        }
      } else {
        localStorage.clear();
        this.class = 'alert alert-danger';
        this.role = 'alert';
        this.message = token.message;
      }
    }, err => {
      console.log('completed');
    }, () => {
      console.log('completed');
    });
  }

  isLoggedIn(){
    return false;
  }
}
