import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-sentenceconstraction',
  templateUrl: './sentenceconstraction.component.html',
  styleUrls: ['./sentenceconstraction.component.scss']
})
export class SentenceconstractionComponent  implements OnInit, OnDestroy  {

  // Error messages variables

  // Hold message after created sentance construction
  Errormessage;
  // Hold message after sentence contruction is deleted
  Errormessage2;
  // hold message after sentance construction is updated
  Errormessage3;

  // Styles to change popup message
  class2;
  role2;
  class3;
  role3;

  // Display user email
  email;

  // Display popup
   display = 'none';
  // Class variable to change color on  notification
  class;
  role;
  // Array to hold all data from sentence constraction table
  SentenceConstruction = [];
  UpdateData = [];


  constructor(private _router: Router, private http: HttpClient) { }
  
  ngOnInit() {
     // tslint:disable-next-line: prefer-const
     let body = document.getElementsByTagName('body')[0];
     body.classList.add('word_construction-page');

     // Run this method each and everytime browser is refreshed
    // Get all data for sentance construction
     this.getData();
     // Verify user before accessing content
     this.VerifyToken();
  }

    // Sending updated data to database
   UpdatingData(SentenceContructionUpdate){
     const submitdata = {Id: this.UpdateData[0].Id, Firstsentance: SentenceContructionUpdate.value.Firstsentance, SecondUpsentance: SentenceContructionUpdate.value.SecondUpsentance }
      this.http.post('http://167.71.93.67:8080/api/UpdateDataSentance', JSON.stringify(submitdata)).subscribe(response =>{
        const message = JSON.parse(JSON.stringify(response));
        this.class3 = 'alert alert-danger';
        this.role3 = 'alert';
        this.Errormessage3 = message.message;
        this.getData();
      })
    }
  

  //Delete
  Delete(deleteid) {
    const deleteSentence = {Id: deleteid };
    this.http.post('http://167.71.93.67:8080/api/deleteSentence', JSON.stringify(deleteSentence)).subscribe(response => {
        const message = JSON.parse(JSON.stringify(response));
        this.display = 'block';
        this.class2 = 'alert alert-danger';
        this.role2 = 'alert';
        this.Errormessage2 = message.message;
        this.getData();
     });
  }

  // Checking sentance to update using ID
  Updatecheck(data) {
    this.UpdateData = [];
    // tslint:disable-next-line: forin
    for (const i in this.SentenceConstruction) {
      // tslint:disable-next-line: prefer-const
      if (this.SentenceConstruction[i].Id === data) {
        const getId = this.SentenceConstruction[i].Id;
        const getallfirstsentance = this.SentenceConstruction[i].Firstsentance;
        const getallsecondsentance = this.SentenceConstruction[i].Secondsentance;
        this.UpdateData.push({Id: getId , Firstsentance: getallfirstsentance, Secondsentance: getallsecondsentance});
      }
    }
    /* localStorage.setItem('Id', this.UpdateData[0].Id);
    localStorage.setItem('Firstsentance', this.UpdateData[0].Firstsentance);
    localStorage.setItem('Secondsentance', this.UpdateData[0].Secondsentance);
    this._router.navigate(['/updatesentence']); */
  }

  // data data to database
  save(SentenceContruction) {
    console.log(SentenceContruction.value);

    if (SentenceContruction.value.sentance === '' || SentenceContruction.value.sentanceContruction === '' ) {
      this.class = 'alert alert-danger';
      this.role = 'alert';
      this.Errormessage = 'Make sure all fields are filled';
    } else {
        this.http.post('http://167.71.93.67:8080/api/sentancecontruction', JSON.stringify(SentenceContruction.value)).subscribe(response => {
        const message = JSON.parse(JSON.stringify(response));
        SentenceContruction.resetForm();
        this.display = 'block';
        this.class = 'alert alert-danger';
        this.role = 'alert';
        this.Errormessage = message.message;
        this.getData();
     });
    }

  }

  // retrieve all data from database for sentance construction

  getData() {
    this.http.get('http://167.71.93.67:8080/api/updatesentenceconstruction').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
      console.log(message);
      // tslint:disable-next-line: forin
      this.SentenceConstruction = [];
      // tslint:disable-next-line: forin

      // looping through all data from response and same to array
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        const getId = message[i].Id;
        const getallfirstsentance = message[i].Firstsentance;
        const getallsecondsentance = message[i].Secondsentance;
        this.SentenceConstruction.push({Id: getId , Firstsentance: getallfirstsentance, Secondsentance: getallsecondsentance});
      }
    });
  }

  // Verify user and check role
  VerifyToken() {
    const Token = {token: localStorage.getItem('token')};
    const role = {role: localStorage.getItem('role')};
    if (localStorage.getItem('token')) {
        this.http.post('http://167.71.93.67:8080/api/authverification', JSON.stringify(Token)).subscribe(response=>{
        const token = JSON.parse(JSON.stringify(response));
          // tslint:disable-next-line: triple-equals
        if ( role.role == token.role ) {
          if(token.role == true)
          {
            this.email = localStorage.getItem('email');
          } else {
            localStorage.clear();
            this._router.navigate(['/']);
          }
        } else {
          localStorage.clear();
          this._router.navigate(['/']);
        }
      });
    } else {
      localStorage.clear();
      this._router.navigate(['/']);
    }
  }

   // Logout user
   logout(){
    localStorage.clear();
    this._router.navigate(['/']);
  }

  ngOnDestroy() {
    // tslint:disable-next-line: prefer-const
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove('word_construction-page');
  }

}
