import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { BrowserModule } from "@angular/platform-browser";
import { Routes, RouterModule } from "@angular/router";

import { IndexComponent } from "./pages/index/index.component";
import { CreatelanguagepageComponent } from "./pages/createlanguage/createlanguage.component";
import { UserRegistration } from "./pages/registerpage/registerpage.component";
import { LandingpageComponent } from "./pages/landingpage/landingpage.component";
import { SiginComponent } from "./sigin/sigin.component";
import { AdminComponent } from "./admin/admin.component";
import { CoursesComponent } from './pages/courses/courses.component';
import {  LessonsComponent } from './pages/lessons/lessons.component';
import {  LessonquestionerComponent } from './pages/lessonquestioner/lessonquestioner.component';
import { SentenceconstractionComponent } from './pages/sentenceconstraction/sentenceconstraction.component';
import { StartcourseComponent } from './pages/startcourse/startcourse.component';

const routes: Routes = [
  { path: "", redirectTo: "home", pathMatch: "full" },
  { path: "home", component: IndexComponent },
  { path: "createLanguage", component: CreatelanguagepageComponent },
  { path: "register", component: UserRegistration },
  { path: "landing", component: LandingpageComponent },
  { path: "signin", component: SiginComponent },
  { path: "Landingpage", component: LandingpageComponent },
  { path: "admin", component: AdminComponent },
  { path: 'course', component: CoursesComponent },
  { path: 'lesson', component: LessonsComponent },
  { path: 'wordAssociation', component: LessonquestionerComponent },
  { path: 'Sentencecontruction', component: SentenceconstractionComponent },
  { path: 'startcourse', component: StartcourseComponent }
];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes, {
      useHash: true
    })
  ],
  exports: []
})
export class AppRoutingModule {}
