    // Update image
    UpdateImage(files: any){
      // asignn blobimagefile to file item so that i can save it using http
      // tslint:disable-next-line: deprecatio
     
      if (files.length === 0) {
        return;
      }
      // tslint:disable-next-line: prefer-const
      // getting type of image
      const mimeType = files[0].type;
      // check if file is image format
      if (mimeType.match(/image\/*/) == null) {
        this.message = 'Only images are supported.';
        return;
      }
      // tslint:disable-next-line: prefer-const
      let reader = new FileReader();
      reader.readAsDataURL(files[0]);
      // tslint:disable-next-line: variable-name
      // load image to the view
      reader.onload = (_event) => {
        this.imgURLUpfdate = reader.result;
        this.oldImage = 'Old One';
        this.newImage = 'New One';
      };
    }
    back(){
      this._router.navigate(['/lesson']);
    }
