import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router'; 

@Component({
  selector: 'app-courses',
  templateUrl: './courses.component.html'
})
export class CoursesComponent implements OnInit, OnDestroy  {
  isCollapsed = true;
  Languages = [];
  courses = [];
  Errormessage;
  emailad;

  //add class and role to message alert
  role;
  class;

  constructor(private _router: Router,private http: HttpClient) { }

  ngOnInit() {
    // tslint:disable-next-line: prefer-const
    let body = document.getElementsByTagName('body')[0];
    body.classList.add('course-page');
    this.getCoursesData();
    this.VerifyToken();
    this.getData();
  }
  logout() {
    localStorage.clear();
    this._router.navigate(['/']);
  }
  createCourse(CreateCourse) {
    this.class = 'alert alert-danger';
    this.role = 'alert';
    // tslint:disable-next-line: no-conditional-assignment
    // tslint:disable-next-line: triple-equals
    if ( CreateCourse.value.coursename == '' || CreateCourse.value.language == ''  ) {
      this.Errormessage = 'Please fill the form';
    } else {
      this.http.post('http://167.71.93.67:8080/api/course', JSON.stringify(CreateCourse.value)).subscribe(response => {
        CreateCourse.resetForm();
        const message = JSON.parse(JSON.stringify(response));
        this.Errormessage = message.message;
        this.getCoursesData();
      });
    }
  }
  ngOnDestroy() {
    // tslint:disable-next-line: prefer-const
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove('course-page');
  }
  getData() {
    this.http.get('http://167.71.93.67:8080/api/GetActivelanguages').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
        // tslint:disable-next-line: forin
      for (const i in message) {
          // tslint:disable-next-line: prefer-const
          let getallLanguages = message[i].Language;
          this.Languages.push({Language: getallLanguages});
        }
    });
  }
  getCoursesData() {
    this.http.get('http://167.71.93.67:8080/api/courses').subscribe(data =>{
      const message = JSON.parse(JSON.stringify(data));
      this.courses.length = 0;
      // tslint:disable-next-line: forin
      // tslint:disable-next-line: forin
      for (const i in message) {
        // tslint:disable-next-line: prefer-const
        let getallcourses = message[i].courses;
        let getallLanguages= message[i].Language;
        this.courses.push({ courses: getallcourses , Language: getallLanguages  });
      }
    });
  }
  VerifyToken() {
    const Token = {token: localStorage.getItem('token')};
    const role = {role: localStorage.getItem('role')};
    if (localStorage.getItem('token')) {
        this.http.post('http://167.71.93.67:8080/api/authverification', JSON.stringify(Token)).subscribe(response=>{
        const token = JSON.parse(JSON.stringify(response));
          // tslint:disable-next-line: triple-equals
        if ( role.role == token.role ) {
          if(token.role == true)
          {
            this.emailad = localStorage.getItem('email');
          } else {
            localStorage.clear();
            this._router.navigate(['/']);
          }
        } else {
          localStorage.clear();
          this._router.navigate(['/']);
        }
      });
    } else {
      localStorage.clear();
      this._router.navigate(['/']);
    }
  }

}
